/* eslint-disable react-hooks/exhaustive-deps */
/* eslint-disable array-callback-return */
import React, { useState, useEffect } from "react";

import { wordings } from "./wordings";
import "./dice.scss";
import DicePlayerScoreCard from "../../components/dice-comp/player-scorecard/player-scorecard";
import Button from "../../components/elements/Button/button";
import DiceRoll from "../../components/dice-comp/dice-roll/dice-roll-";
import WinnerComp from "../../components/dice-comp/winner-comp/winner-comp";
import Settings from "../../components/dice-comp/dicce-settings/dice-settings";
import { Temp } from "../../types/index";

const Dice: React.FC<{}> = () => {
  document.title = wordings.documentTitle;
  const [tempStore, setTempstore] = useState<Temp>({
    player1: "",
    player2: "",
    toWin: 0,
  });
  const [diceNumber, setDiceNumber] = useState<number>(0);
  const [currentScore, setCurrentScore] = useState<number>(0);
  const [activePlayer, setActivePlayer] = useState<number>(0);
  const [settings, setSettings] = useState<boolean>(false);
  const [maxScore, setMaxScore] = useState<number>(50);
  const [mainScore, setMainScore] = useState([0, 0]);
  const [matchWon, setMatchWon] = useState<any>("");
  const [players, setPlayers] = useState(["Player 1", "Player 2"]);

  const setDiceHandler = () => {
    const dice = Math.floor(Math.random() * 6) + 1;
    setDiceNumber(dice);
    setCurrentScore(currentScore + dice);
  };

  const resetHandler = () => {
    setActivePlayer(0);
    setCurrentScore(0);
    setMainScore([0, 0]);
  };

  const changeSettingsHandler = (
    evt: React.MouseEvent<HTMLElement, MouseEvent>
  ) => {
    evt.preventDefault();
    const { player1, player2, toWin } = tempStore;
    if (toWin === 0) {
      setMaxScore(50);
    } else {
      setMaxScore(Number(toWin));
    }
    if (player1 && player2) {
      setPlayers([player1, player2]);
    }
    resetHandler();
    setSettings(false);
  };
  const setPlayersandScore = (evt: any) => {
    const { name, value } = evt.target;

    setTempstore({
      ...tempStore,
      [name]: value,
    });
  };
  useEffect(() => {
    if (diceNumber === 1) {
      setCurrentScore(0);
      if (activePlayer === 0) {
        setActivePlayer(1);
      } else {
        setActivePlayer(0);
      }
    }
  }, [diceNumber]);

  useEffect(() => {
    mainScore.map((el, i) => {
      if (el >= maxScore) {
        setMatchWon({ player: players[i], score: mainScore[i] });
        resetHandler();
      }
    });
  }, [mainScore]);

  const playGround = (
    <div className="main-dice">
      <div
        onClick={() => {
          setSettings(true);
        }}
        className="dice-settings"
      >
        <span className="gear"></span>
      </div>
      <div className="dice">
        <DicePlayerScoreCard
          player={players[0]}
          mainScore={mainScore[0]}
          currentScore={!activePlayer ? currentScore : 0}
          active={!activePlayer ? true : false}
        />

        <DiceRoll dice={diceNumber} />

        <DicePlayerScoreCard
          player={players[1]}
          mainScore={mainScore[1]}
          currentScore={activePlayer ? currentScore : 0}
          active={activePlayer ? true : false}
        />
      </div>
      <div className="dice-buttons">
        <Button name="Roll Dice" clickHandler={setDiceHandler} />
        <Button
          name="Pass"
          clickHandler={() => {
            setMainScore([]);
            if (activePlayer === 0) {
              setActivePlayer(1);
              setMainScore([mainScore[0] + currentScore, mainScore[1]]);
            } else {
              setActivePlayer(0);
              setMainScore([mainScore[0], mainScore[1] + currentScore]);
            }
            setCurrentScore(0);
          }}
        />
        <Button name="Reset" clickHandler={resetHandler} />
        <Button name="Exit" />
      </div>
    </div>
  );
  const winnerComp = (
    <WinnerComp
      player={matchWon.player}
      totalScore={matchWon.score}
      goBackHandler={() => {
        resetHandler();
        setMatchWon("");
      }}
    />
  );
  return (
    <React.Fragment>
      <h2 className="game-name">Far Away From Dice</h2>
      {!matchWon ? playGround : winnerComp}
      <div>
        {settings ? (
          <Settings
            changeHandler={setPlayersandScore}
            submitHadler={changeSettingsHandler}
            closeHandler={() => {
              setSettings(!settings);
            }}
          />
        ) : null}
      </div>
    </React.Fragment>
  );
};

export default Dice;
