import React from "react";

import "./dice-roll.scss";
import dice1 from "../../../assets/dice/dice-1.png";
import dice2 from "../../../assets/dice/dice-2.png";
import dice3 from "../../../assets/dice/dice-3.png";
import dice4 from "../../../assets/dice/dice-4.png";
import dice5 from "../../../assets/dice/dice-5.png";
import dice6 from "../../../assets/dice/dice-6.png";
import diceWelcome from "../../../assets/dice/dice-welcome.png";

interface Props {
  dice: number;
}

const DiceRoll: React.FC<Props> = ({ dice }) => {
  const diceImagesArray = [
    diceWelcome,
    dice1,
    dice2,
    dice3,
    dice4,
    dice5,
    dice6,
  ];

  return (
    <React.Fragment>
      <div className="dice-roll">
        <div className="dice-roll-dice">
          <img src={diceImagesArray[dice]} alt="dice" />
        </div>
      </div>
    </React.Fragment>
  );
};

export default DiceRoll;
